import React from "react"

//props
//life cycle
//this
type AProps = {}
type AState = {
    totalScore: number
}
type BProps = {}
type BState = {

    totalScore: number
}
type CProps = {}
type CState = {
    totalScore: number
}
function getScoreCurrent( ) {
    return 500
}

function getScoreByName(boardName:string) {
    if(boardName === "boardA"){
        return 300
    }
    if(boardName === "boardB" ){
        return 1000
    }
    return 0
}

class ScoreA extends React.Component<AProps, AState> {
    constructor(props: AProps){
        super(props)
        this.state = {
            totalScore: 0
        }
    }
    componentDidMount() {
        this.setState({
            totalScore: getScoreCurrent() + getScoreByName('boardA')
        })
    }
    render(): React.ReactNode {
        return <>
            <p> A Total Score: {this.state.totalScore}</p>
        </>
    }
}
class ScoreB extends React.Component<BProps, BState> {
    constructor(props: BProps){
        super(props)
        this.state = {
            totalScore: 0
        }
    }
    // component setep time
    componentDidMount() {
        this.setState({
            totalScore: getScoreCurrent() + getScoreByName('boardB')
        })
    }
    render(): React.ReactNode {
        return <>
            <p> B Total Score: {this.state.totalScore}</p>
        </>
    }
}


class ScoreC extends React.Component<CProps, CState>{
    constructor(props: CProps){
        super(props)
        this.state = {
            totalScore: 0
        }
    }
    componentDidMount(){
        this.setState({
            totalScore: getScoreCurrent() + getScoreByName('boardC')
        })
    }
    render() {
        return<>
            <p>C Total Score: {this.state.totalScore}</p>
        </>
    }
}
function HOC() {
    
}
function Main() {
    return <>
        <ScoreA />
        <ScoreB />
        <ScoreC />
    </>
}

export default Main

